import React, { Component } from 'react';
import TitleBar from '../../components/TitleBar';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import styled from 'styled-components';
import GenderType from '../../components/GenderType';
import {connect} from 'react-redux';
import * as AuthReducers from '../../modules/authentication';
import { bindActionCreators } from 'redux';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import defaultProfile from '../../images/default-profile.jpg';

const Wrapper = styled.div`
    position : relative;
`;
const Box = styled.div`
    width : 770px;
    
    position : fixed;
    top : -50px; left : 0; right : 0; bottom : 0;
    margin : auto;
    z-index : -10;
    background-color : #efefef;
`;

const FormWrapper = styled.form`
    width : 500px;
    height : 700px;

    margin : 0 auto;
    margin-top : 4rem;
    display : flex;
    flex-direction : column;


    .row {
        position : relative;
        display : flex;
        align-items : center;
        justify-content : space-between;
        margin-bottom : 1rem;
        Button {
            margin-left : 2rem;
        }
    }

    .warning-text {
        color : red;
        font-size : 12px;
        font-weight : 600;
    }

    .none {
        display : none;
    }
    
    .prev-image {
        width : 120px;
        height : 110px;
        border-radius : 50%;
    }

    /* .prev-image-title {
        position : absolute;
        right : 59px;
        bottom : -15px;
        font-size : 10px;
        font-weight : 600;

    }

    
    .btn-add-profile {
        position : absolute;
        color : #F50057;
        right : 180px;
        bottom : 0;
        &:hover {
            color : #C51162;
        }
    } */

    .row-wrapper {
        margin-top : 20px;
        margin-bottom : 20px;
        width : 400px;
        border : 1px solid black;
        display : flex;
        flex-direction : column;
        padding : 10px 20px;
        &>* {
            margin-bottom : 20px;
        }
    }

    .profile-row-wrapper {
        width : 400px;
        margin-top : 20px;
        margin-bottom : 20px;
        display : flex;
        flex-direction : column;
        align-items : center;
        justify-content : center;
        border : 1px solid black;
        padding : 20px;
        &>* {
            margin-bottom : 20px;
        }
    }
`;

class SignUpCounselor extends Component {

    prevImage = null;   //   ref
    profileRef = null;
    licenseRef = null;
    educationRef = null;

    handleProfileUploadClick = (evt) => {
        this.profileRef.click();
    }
    handleLicenseUploadClick = (evt) => {
        this.licenseRef.click();
    }
    handleEducationUploadClick = () => {
        this.educationRef.click();
    }
    handleSubmit = (evt) => {
        evt.preventDefault();

        //   
        const {
            id, pw1, pw2, name, phone, birth, gender,
            profile, licenseNo, licenseName, license, 
            degree, school, major, graduated, comment,
            educationName, profileName, licensePath, education
        } = this.props.inputData.toJS();
        if(id === '' || pw1 === '' || pw2 === '' || name === '' || phone === '' || birth === '' || gender === '' ||
           profile === '' || profileName ==='  ' || licenseNo === '' || licenseName === '' || license === '' ||
           degree === '' || school === '' || major === '' || graduated === '' || comment === ''||
           educationName === '  ' || licensePath === '  '){
                this.props.authReducers.modalOpen({title:' ', content : '   !'});
                return;
        }

        //  
        if(!this.props.inputData.toJS().validate){
            this.props.authReducers.modalOpen({title:' ', content : '  !'});
            return;
        }

        //    
        if(pw1 !== pw2) {
            this.props.authReducers.modalOpen({title:' ', content : '  !'});
            return;
        }

        //   
        const regExpPw = /^[A-Za-z0-9]{6,20}$/;
        if(!regExpPw.test(pw1)) {
            this.props.authReducers.modalOpen({title:' ', content : '    6~20 .'});
            return;
        }

        //   
        const regExpName = /^[-]{2,5}|[a-zA-Z]{2,10}\s[a-zA-Z]{2,10}$/;
        if(!regExpName.test(name)) {
            this.props.authReducers.modalOpen({title:' ', content : '   .'});
            return;
        }

        //   
        var regExpPhone = /^\d{3}-\d{3,4}-\d{4}$/;
        if(!regExpPhone.test(phone)) {
            this.props.authReducers.modalOpen({title:' ', content : ' : -   \n(  : 010-1234-1234 )'});
            return;
        }


        const data = {
            id,
            pw1,  
            name,
            phone,
            birth,
            gender : gender === '' ? 1 : 2,
            profile,
            licenseNo,
            licenseName,
            license,
            degree,
            school,
            major,
            graduated,
            comment,
            education
        };

        const { counselorSignUpAction } = this.props.authReducers;

        counselorSignUpAction(data)
            .then(() => {
                const {result} = this.props.inputData.toJS();
                if(result === 'SUCCESS') {
                    this.props.history.push('/authentication/signin');
                    this.props.authReducers.modalOpen({title : ' ', content : ' !\n   .'});
                    this.props.authReducers.counselorSignupInitialize();
                } else {
                    this.props.authReducers.modalOpen({title : 'ERROR', content : ' ,   .'});
                }
            });
    }

    handleValidate = () => {
        const {id} = this.props.inputData.toJS();

        if(id === '') {
            this.props.authReducers.modalOpen({title:' ', content : '  !'});
            return;
        }

        const regExpId = /^[A-Za-z0-9]{5,20}$/;
        if(!regExpId.test(id)) {
            this.props.authReducers.modalOpen({title:' ', content : '    5~20 .'});
            return;
        }

        this.props.authReducers.validateAction(id, "counselor")
            .then(() => {
                const {validate} = this.props.inputData.toJS();
                if(validate === true) {
                    this.props.authReducers.modalOpen({title : ' ', content: '  !'});
                } else {
                    this.props.authReducers.modalOpen({title : ' ', content: ' !'});
                }
            })
    }

    handleFileChange = (evt) => {
        const {authReducers} = this.props;
        const {name, value} = evt.target;

        if (name === 'license') {
            authReducers.counselorSignupInputChanged({name, value : evt.target.files[0]});    //setState
            if(evt.target.files && evt.target.files[0])   //  
                authReducers.counselorSignupInputChanged({name : 'licensePath', value});    //setState
            else 
                authReducers.counselorSignupInputChanged({name : 'licensePath', value : '  '});    //setState
            return;
        }

        if(name === 'education') {
            authReducers.counselorSignupInputChanged({name, value : evt.target.files[0]});    //setState
            if(evt.target.files && evt.target.files[0])   //  
                authReducers.counselorSignupInputChanged({name : 'educationName', value});    //setState
            else 
                authReducers.counselorSignupInputChanged({name : 'educationName', value : '  '});    //setState
            return;
        }

        if(name === 'profile') {
            //     
            if(evt.target.files && evt.target.files[0]) {   //    (  null    )

                //   
                if (value.split('.')[1] !== 'jpg' && 
                    value.split('.')[1] !== 'jpeg' && 
                    value.split('.')[1] !== 'gif' && 
                    value.split('.')[1] !== 'png' && 
                    value.split('.')[1] !== 'bmp') {
                    this.props.authReducers.modalOpen({title : '  ', content : '   .\n(jpg, gif, png, bmp)'});
                    return;
                }

                //  
                if (evt.target.files[0].size > (1024 * 1024 * 5)) {
                    this.props.authReducers.modalOpen({title : '  ', content : ' 5MB   .'});
                    return;
                }

                authReducers.counselorSignupInputChanged({name, value : evt.target.files[0]});    //setState
                authReducers.counselorSignupInputChanged({name : 'profileName', value});    //evt.target.value  
                var reader = new FileReader();  
                reader.onload = (e) => {            // readAsDataURL    
                    let path = e.target.result;
                    this.prevImage.src = path;  //prevImage  ref   prevImage  src  .
                    this.prevImage.style.display = 'inline-block';
                }
                reader.readAsDataURL(evt.target.files[0]);
            } else {
                authReducers.counselorSignupInputChanged({name, value : evt.target.files[0]});    //setState
                authReducers.counselorSignupInputChanged({name : 'profileName', value : "  "});    
                this.prevImage.src = `${defaultProfile}`;
            }
        }
    }


    handleChange = (evt) => {
        const {authReducers}= this.props;

        const {name, value} = evt.target;

        authReducers.counselorSignupInputChanged({name, value});

        if(name === 'pw2') {    //   
            const {pw1} = this.props.inputData.toJS();
            if(pw1 !== value) {
                this.props.authReducers.counselorWarningShow();
            } else {
                this.props.authReducers.counselorWarningHide();
            }
        } else if(name === 'pw1') {
            const {pw2} = this.props.inputData.toJS();
            if(pw2 !== value) {
                this.props.authReducers.counselorWarningShow();
            } else {
                this.props.authReducers.counselorWarningHide();
            }
        }

    }

    

    render() {
        const {handleSubmit, handleChange, handleValidate, handleProfileUploadClick, handleLicenseUploadClick, handleFileChange, handleEducationUploadClick} = this;
        const {id, pw1, pw2, name, phone, birth, gender, warningText, profileName, school, graduated, licenseName, licenseNo, degree, major, educationName, licensePath} = this.props.inputData.toJS();


        return (
            <Wrapper>
                <Box/>
                <TitleBar title=" "/>
                <FormWrapper>
                    <div className="row">
                        <TextField label="" variant="outlined" name='id' value={id} onChange={handleChange}/>
                        <Button variant="contained" color="secondary" onClick={handleValidate}> </Button>
                    </div>
                    <div className="row">
                        <TextField label="" variant="outlined" type="password" name="pw1" value={pw1} onChange={handleChange}/>
                    </div>
                    <div className="row">
                        <TextField label=" " variant="outlined" type="password" name="pw2" value={pw2} onChange={handleChange}/>
                        {
                            warningText === 'SHOW' 
                            ? <span className="warning-text">  .</span>
                            : null
                        }
                    </div>
                    <div className="row">
                        <TextField label="" variant="outlined" name="name" value={name} onChange={handleChange}/>
                    </div>
                    <div className="row">
                        <TextField label="" variant="outlined" name="phone" value={phone} onChange={handleChange}/>
                    </div>
                    <div className="row">
                        <TextField
                            label=""
                            type="date"
                            defaultValue={birth}
                            InputLabelProps={{
                                shrink: true,
                            }}
                            name="birth"
                            onChange={handleChange}
                        />
                    </div>
                    <div className="row">
                        <GenderType name="gender" value={gender} handleChange={handleChange}/>
                    </div>
                    <div className="profile-row-wrapper">
                        <img src={defaultProfile} ref={ref => {this.prevImage = ref}} className="prev-image" alt="  "/>
                        <TextField label=" " disabled={true} name='profileName' value={profileName}/>
                        <Button variant="contained" color="secondary"  onClick={handleProfileUploadClick} name="profileUploadBtn"> </Button>
                        <input type="file" name="profile" className="none" onChange={handleFileChange} ref={ref => {this.profileRef = ref}}/>
                    </div>
                    <div className="row-wrapper">
                        <TextField label="  " variant="standard" name="licenseNo" value={licenseNo} onChange={handleChange} style={{width : '200px'}}/>
                        <TextField label="  " variant="standard" name="licenseName" value={licenseName} onChange={handleChange} style={{width : '200px'}}/>
                        <input type="file" name="license" className="none" onChange={handleFileChange} ref={ref => {this.licenseRef = ref}}/>
                        <TextField label=" " disabled={true} name='licensePath' value={licensePath}/>
                        <Button variant="contained" color="secondary"  onClick={handleLicenseUploadClick} name="licenseUploadBtn"> </Button>
                    </div>
                    <div className="row-wrapper">
                        <FormControl>
                            <InputLabel htmlFor="select-multiple"></InputLabel>
                            <Select
                                value={degree}
                                onChange={this.handleChange}
                                inputProps={{
                                    name: 'degree'
                                }}
                                style={{width : '130px'}}
                                >
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                            </Select>
                        </FormControl>
                        <TextField label="" variant="standard" name="school" value={school} onChange={handleChange} style={{width : '300px'}}/>
                        <TextField label="" variant="standard" name="major" value={major} onChange={handleChange} style={{width : '300px'}}/>
                        <FormControl>
                            <InputLabel htmlFor="select-multiple"> </InputLabel>
                            <Select
                                value={graduated}
                                onChange={this.handleChange}
                                style={{width : '130px'}}
                                inputProps={{
                                    name: 'graduated'
                                }}
                                >
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                                <MenuItem value={""}></MenuItem>
                            </Select>
                        </FormControl>
                        <input type="file" name="education" className="none" onChange={handleFileChange} ref={ref => {this.educationRef = ref}}/>
                        <TextField label=" " disabled={true} name='educationName' value={educationName}/>
                        <Button variant="contained" color="secondary"  onClick={handleEducationUploadClick} name="educationUploadBtn">  </Button>
                    </div>
                    <div className="row">
                    <TextField  //  
                        id="outlined-full-width"
                        label="   "
                        style={{ margin: 8, marginTop : 10}}
                        placeholder="    ."
                        fullWidth
                        margin="normal"
                        variant="outlined"
                        InputLabelProps={{
                            shrink: true,
                        }}
                        name="comment"
                        onChange={handleChange}
                        />
                    </div>
                    <Button variant="contained" color="primary" style={{minHeight : '80px'}} onClick={handleSubmit} styles={{marginBottom : '50px', minHeight : '50px'}}> </Button>
                </FormWrapper>
            </Wrapper>
        );
    }
}

export default connect(
    (state) => ({
        inputData : state.authentication.getIn(['signupData', 'counselor']),
        licenseOfNum : state.authentication.get('licenseOfNum')
    }),
    (dispatch) => ({
        authReducers : bindActionCreators(AuthReducers, dispatch)
    })
)(SignUpCounselor);